<template>
  <div v-ban-save>
    <form action="javascript:return true">
      <van-search
        v-model="searchValue"
        show-action
        shape="round"
        placeholder="搜索商品"
        @search="onSearch">
        <template #action>
          <div @click="onSearch('')" class="vsearch">搜索</div>
        </template>
      </van-search>
    </form>
    <img style="position: absolute; top: 19px; width: 15px; height: 15px; left: 22px;" :src="require('@/assets/image/search.png')" alt="" width="46px" height="46px">
    <div class="shopMsg">
      <!-- 首页banner -->
      <div class="rotation">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" style="height: 150px;">
          <van-swipe-item v-for="item in bannerList" :key="item.id" @click="commodityClick(item.id, item.url)">
            <img :src="item.pic" alt=""/>
          </van-swipe-item>
        </van-swipe>
      </div>
      <!-- 分类 -->
      <van-row type="flex" justify="space-between" style="width: 94%;height:190px;margin:10px 3%">
        <van-col span="5" v-for="(item, indexcs) in categoryTreeList" :key="item.id" @click="goClassfyList(item.id)">
          <div class="shopTab">
            <img :src="item.mobileIcon" alt="" width="46px" height="46px">
            <p :style="indexcs === 3 ? 'width: 90px;' : ''">{{item.name}}</p>
          </div>
        </van-col>
        <van-col span="5" @click="goClassfyList('')">
          <div class="shopTab">
            <img :src="require('@/assets/image/more.png')" alt="" width="46px" height="46px">
            <p>更多</p>
          </div>
        </van-col>
      </van-row>
      <!-- 新品推荐 -->
      <div class="rmhh">
        <img :src="require('@/assets/image/xptj.jpg')" alt="" width="97px" height="21px">
      </div>
      <div class="recomShop">
        <van-tabs v-model="activeIndexXp" sticky @click="getProductListXp" color="#FD2744" animated>
          <van-tab v-for="item in newTreeList" :key="item.name" :name="item.id">
            <template #title>
              <div class="superShop center">
                <div class="title" :class="item.id === activeNumXp ? 'titleActive' : '' ">{{item.name}}</div>
                <!-- <div class="xiabiao" :class="item.id === activeNumXp ? 'active' : '' " style="height: 4px; top: 30px;"></div> -->
              </div>
            </template>
            <div v-if="productXpList.length > 0" class="box">
            <div class="recomList">
                <template>
                  <div class="listTab" @click="goShop(item)" v-for="item in productXpList" :key="item.id">
                    <img :src="item.pic" alt="" width="103px" height="103px">
                    <p class="shoptitle">{{item.name}}</p>
                    <p class="shopPrice">{{item.offlineBuy === 1 ? '线下购买' : '￥' + item.price}}</p>
                  </div>
                </template>
              </div>
              <div class="more" @click="toNewProduct(item.id)" v-if="xPtoTal>0">查看更多</div>
            </div>
            <div v-else class="empty">
              <img :src="require('@/assets/image/empty.png')" alt="">
              <div>该类目暂无商品<br>敬请期待</div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
      <!-- 特价专区 -->
      <div class="rmhh">
        <img :src="require('@/assets/image/tjzq.jpg')" alt="" width="92px" height="21px"><span>超值优惠享不停</span>
      </div>
      <div class="recomShop">
        <van-tabs v-model="activeIndexTj" sticky @click="getProductListTj" color="#FD2744" animated>
          <van-tab v-for="item in newTreeList" :key="item.name" :name="item.id">
            <template #title>
              <div class="superShop center">
                <div class="title" :class="item.id === activeIndexTj ? 'titleActive' : '' ">{{item.name}}</div>
                <!-- <div class="xiabiao" :class="item.id === activeNumXp ? 'active' : '' " style="height: 4px; top: 30px;"></div> -->
              </div>
            </template>
            <div v-if="productTjList.length > 0" class="box">
              <div class="recomList">
                  <div class="listTab" @click="goShop(item)" v-for="item in productTjList" :key="item.id">
                    <img :src="item.pic" alt="" width="103px" height="103px">
                    <p class="shoptitle">{{item.name}}</p>
                    <p class="shopPrice">{{item.offlineBuy === 1 ? '线下购买' : '￥' + item.price}}</p>
                  </div>
              </div>
              <div class="more" @click="toSpecials(item.id)" v-if="tJtoTal>0">查看更多</div>
            </div>
            <div v-else class="empty">
              <img :src="require('@/assets/image/empty.png')" alt="">
              <div>该类目暂无商品<br>敬请期待</div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
      <!-- 热卖好货 -->
      <div class="rmhh">
        <img :src="require('@/assets/image/px.png')" alt="" width="93px" height="18px">
      </div>
      <div class="border"></div>
      <div class="hotShop mg">
        <van-tabs v-model="activeIndexRm" sticky background="#f7f7f7" @click="tabList" animated style="margin-top: 12px;">
          <van-tab style="min-height: 300px;" v-for="item in treeList" :key="item.name" :name="item.id">
            <template #title>
              <div class="superShop center">
                <div class="title" :class="item.id === activeNumRm ? 'titleActive' : '' ">{{item.name}}</div>
                <!-- div class="xiabiao" :class="item.id === activeNumRm ? 'active' : '' ">超值商品</div -->
              </div>
            </template>
            <div class="msgBottom" v-if="productRmList.length > 0">
              <div class="imgShop" @click="goShop(dto)" v-for="dto in productRmList" :key="dto.id">
                <div class="shopImg">
                  <van-image :src="dto.pic" class="img"/>
                  <div v-if="dto.memberDiscount" class="discount">享{{discountDetails(dto.memberDiscount,10)}}折</div>
                </div>
                <div class="shopInformation">
                  <h1>{{dto.name}}</h1>
                  <p v-if="dto.offlineBuy === 0 && dto.offlineBuy !== 1">￥{{dto.price}}</p>
                  <p v-else>线下购买</p>
                </div>
              </div>
            </div>
            <div class="empty" v-else style="margin-top: 20px;">
              <img :src="require('@/assets/image/empty.png')" alt="">
              <div class="emptyFont">该类目暂无商品<br>敬请期待</div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
    <div style="width:100%;height:50px"></div>
    <div class="footer">
      <div class="firstPage" @click="toPages('0')">
        <img :src="require('@/assets/image/xzsy.png')" alt="" />
        <p class="ptitle">首页</p>
      </div>
      <div class="sort" @click="toPages('1')">
        <img :src="require('@/assets/image/wxzfl_gray.png')" alt="" />
        <p>分类</p>
      </div>
      <div class="shopCart" @click="toPages('2')">
        <img :src="require('@/assets/image/wxzgwc_gray.png')" alt="" />
        <p>购物车</p>
      </div>
      <div class="myMessage" @click="toPages('3')">
        <img :src="require('@/assets/image/wxzmy_gray.png')" alt="" />
        <p>我的</p>
      </div>
    </div>
    <Service></Service>
  </div>
</template>

<script>
import Service from '@/components/Layout/service/index'
import * as api from '@/data/api/market/index'

export default {
  name: 'ProductList',
  components :{
    Service
  },
  data () {
    return {
      activeIndexTj:0,
      activeIndexRm:0,
      activeIndexXp:0,
      searchValue : '',
      bannerList: [],
      activeNumRm:0,
      activeNumTj: 0,
      activeNumXp: 0,
      treeList:[],
      productXpList:[],
      productTjList:[],
      productRmList: [],
      categoryTreeList:[],
      newTreeList:[],
      xPtoTal:0,
      tJtoTal:0,
      wxUrl: process.env.wxUrl
    }
  },
  mounted () {
    let me = this
    me.getBanner()
    me.getTreeList()
    me.getNewProducts()
  },
  methods: {
    getNewProducts(){
      let me = this
      api.categoryTreeRecommendList().then(res => {
        if (res.code === 200) {
          me.newTreeList = res.data
          me.getProductListXp(res.data[0].id)
          me.getProductListTj(res.data[0].id)
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      })
    },
    async getProductListTj(id){
      let me = this
      me.activeNumTj = id
      try {
          const res = await api.getSearchHotProduct({
            pageNum: 1,
            pageSize: 3,
            productCategoryId: id,
            type: 2
          });
          if (res.code === 200) {
              this.productTjList = res.data.list
              this.tJtoTal = res.data.total
            } else {
              me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        } catch (error) {
              me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
    },
    async getProductListXp(id){
      let me = this
      me.activeNumXp = id
      try {
        const res = await api.getSearchRecommend({
          pageNum: 1,
          pageSize: 3,
          productCategoryId: id,
          type: 2
        });
        if (res.code === 200) {
            this.productXpList = res.data.list
            this.xPtoTal = res.data.total
          } else {
            me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      } catch (error) {
          me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    commodityClick (id, url) {
      if (url) {
        window.location.href = url
      } else {
        this.$router.push({
          path:'/market/product',
          query: {id: id}
        })
      }
    },
    toSpecials(id){
      this.$router.push({
        path:'/market/specials',
        query:{
          'id': id
        }
      })
    },
    toNewProduct(id){
      this.$router.push({
        path:'/market/newProduct',
        query:{
          'id': id
        }
      })
    },
    tabList(name){
      this.activeNumRm = name
      this.itemsListDetail(name)
    },
    getTreeList () {
      let me = this
      me.treeList = []
      api.getCategoryTreeList().then(res => {
        if (res.code === 200) {
            // me.treeList = res.data
            me.categoryTreeList = res.data.slice(0,7)
            res.data.forEach((item)=>{
              if (item.name == "司法鉴定") {
                me.treeList = item.children
              }
            })
            me.activeNumRm = me.treeList[0].id
            me.itemsListDetail(me.treeList[0].id)
          
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      })
    },
    itemsListDetail (id) {
      let me = this
      me.productRmList = []
      api.getSearch({
        pageNum: 1,
        pageSize: 100,
        productCategoryId: id,
        recommandStatus: 1,
        level:''
      }).then(res => {
        if (res.code === 200) {
          me.productRmList = res.data.list
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      })
    },
    getBanner () {
      let me = this
      me.bannerList = []
      api.homeContent({type: '2'}).then(res => {
          if (res.code === 200) {
            res.data.advertiseList.map(item =>{
              me.bannerList.push(item)
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        })
    },
    toPages (value) {
      if (value === '0') {
        this.$router.push({
          path:'/market/home'
        })
      } else if (value === '1') {
        this.$router.push({
          path:'/market/classify'
        })
      } else if (value === '2') {
        if (localStorage.getItem('token')) {
          this.$router.push({
            path:'/market/cart'
          })
        } else {
          const { userAgent } = navigator
          this.isWeixin = !!/MicroMessenger/i.test(userAgent)
          // !localStorage.getItem('token') && 
          if (this.isWeixin) {
            let st = {
              name: 'home',
              url: '/market/cart'
            }
            window.location.href="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx372e6d05b22ce032&redirect_uri=" + this.wxUrl + "&response_type=code&scope=snsapi_base&state=" + JSON.stringify(st) + "#wechat_redirect"
          } else {
            localStorage.setItem('urlPath','/market/cart')
            this.$router.push({
              path:'/user/login'
            })
          }
        }
      } else if (value === '3') {
        if (localStorage.getItem('token')) {
          this.$router.push({
            path:'/user/home'
          })
        } else {
          const { userAgent } = navigator
          this.isWeixin = !!/MicroMessenger/i.test(userAgent)
          // !localStorage.getItem('token') && 
          if (this.isWeixin) {
            let st = {
              name: 'home',
              url: '/user/home'
            }
            window.location.href="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx372e6d05b22ce032&redirect_uri=" + this.wxUrl + "&response_type=code&scope=snsapi_base&state=" + JSON.stringify(st) + "#wechat_redirect"
          } else {
            localStorage.setItem('urlPath','/user/home')
            this.$router.push({
              path:'/user/login'
            })
          }
        }
      }
    },
    goClassfyList (id) {
      if (id) {
        this.$router.push({
          path:'/market/list',
          query:{
            'oid': id,
            'id': ''
          }
        })
      } else {
        this.$router.push({
          path:'/market/classify',
          query: {
            'id': id
          }
        })
      }
    },
    goShop(dto){
      if (dto.id) {
        this.$router.push({
          path:'/market/product',
          query: {id: dto.id}
        })
      }
    },
    onSearch (value) {
      let me = this
      me.$router.push({
        path:'/market/list',
        query:{
        'search': value || me.searchValue
        }
      })
    },
    discountDetails (arg1, arg2) {
      if (arg1 && arg2) {
        let m = 0,
          s1 = arg1.toString(),
          s2 = arg2.toString();
        try {
          m += s1.split('.')[1].length;
        } catch (e) {}
        try {
          m += s2.split('.')[1].length;
        } catch (e) {}
        return (
          (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) /
          Math.pow(10, m)
        )
      }
    }
  }
}
</script>
