.superShop{
  height: 40px
  .title{
    font-weight: bold
    font-size: 16px
    color: #444
  }
  .xiabiao{
    width: 60px;
    height: 18px;
    border-radius: 8px;
    color #666
    font-size: 12px
    line-height: 18px
    position: absolute
    text-align: center
    top 26px
    z-index: 999
    left: calc(50% - 30px)
    justify-content: center;
    display: flex;
  }
  .titleActive{
    color:#FD2744
  }
  .active{
    color: white;
    background:  #FD2744
  }
}
.empty{
  height:170px
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  margin-top: 10px
  border-radius: 10px
  background-color:#fff
  img{
    width:100px;
    height: 70px
  }
  div{
    text-align: center
    margin 0px auto
    font-size: 13px;
    color: #777777;
    line-height: 20px;
  }
}
.footer {
  position fixed
  bottom 0px
  width: 100%;
  height: 49px;
  background: #fff;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.05);
  display flex;
  z-index: 999;
  justify-content space-around
  .firstPage,.sort,.shopCart,.myMessage {
    font-size 10px
    width: 44px;
    height: 37px; 
    margin 6px 0px
    text-align center
  }
  img{
    width 20px;
    height 20px
  }
  .ptitle {
    color: #FF4C4C;
  }
  p{
    color #444
    margin-top 4px
  }
}
</style>


